import React, { useEffect, useState } from 'react';
import { createArticleApi, getArticleByIdApi, updateArticleApi } from '../../apis/article';
import "./index.scss"
import { Button, Form, Input, Breadcrumb, Select, Upload, Radio, message } from 'antd';
import { Option } from 'antd/es/mentions';
//导入富文本
import ReactQuill from 'react-quill';
import "react-quill/dist/quill.snow.css"
import { useChannel } from '../../hooks/useChannel';
import { useNavigate, useSearchParams } from 'react-router-dom';


const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
};
const Publish = () => {
    const navigate=useNavigate()
    const { channelList } = useChannel()
    //收集表单数据
    const onFinish =  (values) => {
        //检验封面类型imageType是否和时机的图片列表imageList数量相等
        if (imageList.length !== imageType) return message.warning("封面类型和图片数量不匹配")
        //把表单数据拿出来
        const { title, content, channel_id } = values
        console.log('Success:', values);
        //按照接口文档的格式处理表单数据
        const reqData = {
            title: title,
            content: content,

            cover: {
                type: imageType,
                //新增时候需要的数据类型是这样的
                //但是更新不是这样的，需要做参数适配
                images: imageList.map(item => {
                    if (item.response)
                        return item.response.data.url  //新增的
                    else
                        return item.url  //更新的
                    
                })//图片列表
            },
            channel_id: channel_id,
        }
        //调用接口提交
        //新增调新增接口，更新调更新接口
        if(articleId)
        {
            updateArticleApi({...reqData,id:articleId})
            message.success("更新成功!")
            navigate("/article")
        }
        else
        createArticleApi(reqData)
        
    };

    //上传回调
    const [imageList, setImageList] = useState([])
    const onUpLoadChange = (value) => {
        // console.log("上传中",value);
        setImageList(value.fileList)
    }
    //封面的条件渲染
    const [imageType, setImageType] = useState(0)
    const onTypeChange = (e) => {
        setImageType(e.target.value)
        // console.log(e.target.value);
    }
    const [searchParams] = useSearchParams()
    const articleId = searchParams.get("id")
    //获取实例
    let [form] = Form.useForm()
    //回填数据
    useEffect(() => {
        //通过id获取数据
        async function getArticleDetail() {
            const res = await getArticleByIdApi(articleId)
            console.log(res.data);
            //回填
            form.setFieldsValue({
                ...res.data,
                //type不在第一层，这里是拿出来发到第一层
                type: res.data.cover.type,
            })
            //回填图片类型
            setImageType(res.data.cover.type)
            //回填图片
            setImageList(res.data.cover.images.map(url => {
                return {
                    url
                }
            }))
        }
         //如果有id就获取信息
        if (articleId)
            getArticleDetail()

    }, [articleId, form])

    return (
        <div>
            {/* 面包屑 */}
            <Breadcrumb
                style={{
                    margin: '16px 0',
                }}
            >
                <Breadcrumb.Item>首页</Breadcrumb.Item>
                <Breadcrumb.Item>{articleId ? "编辑" : "发布"}文章</Breadcrumb.Item>
            </Breadcrumb>
            {/* 表单 */}
            <Form

                name="basic"
                labelCol={{
                    span: 8,
                }}
                wrapperCol={{
                    span: 16,
                }}
                style={{
                    maxWidth: 600,
                }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                autoComplete="off"
                form={form}
            >
                <Form.Item
                    label="标题"
                    name="title"
                    rules={[
                        {
                            required: true,
                            message: '请输入标题!',
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                {/* 选择框 */}
                <Form.Item
                    name="channel_id"
                    label="频道"
                    rules={[
                        {
                            required: true,
                        },
                    ]}
                >
                    <Select
                        placeholder="请选择频道"
                    >

                        {
                            // value收集用户的输入
                            channelList.map((item) => <Option key={item.id} value={item.id}>{item.name}</Option>)
                        }
                    </Select>
                </Form.Item>
                <Form.Item label="封面">
                    <Form.Item name="type"
                        initialValue={imageType}>
                        <Radio.Group onChange={onTypeChange}>
                            <Radio value={1}>单图</Radio>
                            <Radio value={3}>三图</Radio>
                            <Radio value={0}>无图</Radio>
                        </Radio.Group>
                    </Form.Item>
                    {/* 条件渲染 */}
                    {imageType > 0 && <Upload listType='pricture-card'
                        // 设置上传数量属性
                        maxCount={imageType}
                        showUploadList
                        initialValues={0}
                        action={"http://toutiao.itheima.net/v1_0/upload"}
                        name="image"
                        fileList={imageList}
                        onChange={onUpLoadChange}>
                        <div style={{ marginTop: 8 }}>
                            <p style={{ position: "relative", width: "80px", height: "80px", borderRadius: "10px", backgroundColor: "#ccc" }}>
                                <p style={{ fontSize: "80px", textAlign: "center", color: "white", position: "absolute", top: "-112px", left: "13px" }}>+</p>
                            </p>
                        </div>
                    </Upload>}
                </Form.Item>
                {/* 内容 */}
                <Form.Item
                    label="内容"
                    name="content"
                    rules={[
                        {
                            required: true,
                        },
                    ]}
                >
                    <ReactQuill
                        className='publish-quill'
                        theme="snow"
                        placeholder='请输入文章内容'
                    ></ReactQuill>
                </Form.Item>
                <Form.Item
                    wrapperCol={{
                        offset: 8,
                        span: 16,
                    }}
                >
                    <Button type="primary" htmlType="submit">
                        发布文章
                    </Button>
                </Form.Item>
            </Form>
        </div>
    )
}
export default Publish

